.chat-box-header {
  position: relative;
  height: 50px;
  border-top-left-radius: 3px;
  border-top-right-radius: 3px;
  color: white;
  padding-left: 10px;
  font-size: 20px;
  padding-top: 13px;
}

.chat-box-body {
  position: relative;
  height: 370px;
  height: auto;
  border-bottom: 1px solid rgba(181, 181, 181, 0.42);
  overflow: hidden;
}

.chat-box-body:after {
  content: "";
  background-color: #f8f8f8;
  opacity: 0.1;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  height: 100%;
  position: absolute;
  z-index: -1;
}

.chat-input__text {
  background: #fff;
  width: 95%;
  position: relative;
  height: 47px;
  padding-top: 10px;
  padding-right: 50px;
  padding-bottom: 10px;
  padding-left: 15px;
  margin-top: 5px;
  resize: none;
  outline: none;
  border: 1px solid #f2f0f0;
  color: #888;
  border-radius: 3px;
  overflow: hidden;
}

.chat-input-box > form {
  margin-bottom: 0;
}

.chat-input__text::-webkit-input-placeholder {
  /* Chrome/Opera/Safari */
  color: #ccc;
}

.chat-input__text::-moz-placeholder {
  /* Firefox 19+ */
  color: #ccc;
}

.chat-input__text:-ms-input-placeholder {
  /* IE 10+ */
  color: #ccc;
}

.chat-input__text:-moz-placeholder {
  /* Firefox 18- */
  color: #ccc;
}

